import React, { PureComponent, Fragment } from 'react';
import {connect} from 'react-redux';
import folderCss from './folder.scss';
import classNames from 'classnames';
// import * as actions from '../../actions';
import SubWindow from '../subwindow';

const scss = classNames.bind(folderCss);
class Folder extends PureComponent {

    handleClickEvt = (value,index) => {
        switch (value) {
            case 'tistory':
                if(!window.confirm('블로그로 이동하시겠습니까?')) return false;
                return window.open('http://backback.tistory.com/','tistory');
                case 'github':
                if(!window.confirm('깃 허브로 이동하시겠습니까?')) return false;
                return window.open('https://github.com/FactAttacker?tab=repositories/','github');    
            default:
                this.props.folderOpen(index, this.props.folderSelected(index));
                break;
        }
    }

    handleShowFolder = () =>{
        const folders = this.props.redux_folder.folder;
        const liList = (data,index) => {
            return(
                <li key={'folder-list-'+index}>
                    <div className={scss('folder')}
                        onClick={()=>{this.handleClickEvt(data.name,index)}}>
                        <img src={data.imgUrl} alt={data.text}/>
                        <div className={scss('text')}>
                            <span>{data.text}</span>
                        </div>
                    </div>
                </li>
            );
        }

        const ulGroup = (rowData,index) =>(
            <ul className="folder_row" 
                key={'folder-group-'+index}> {rowData} </ul>
        );
            
        let arrayList = [];
        let rowData   = [];
        let arrayNum  = folders.length;
        for(let i =0 ; arrayNum > i ; i++){
            if( i !==0 && i%3 === 0){ // 행 갯수 
                // console.log(" folder :"+ folders[i].name);
                arrayList.push(ulGroup(rowData,i));
                rowData = [];
            }
            rowData.push(liList(folders[i],i)); 
            if(arrayNum === (i+1)) arrayList.push(ulGroup(rowData,(i+1)));
        };
        return arrayList;
    }
    
    render(){
        return(
            <Fragment>
                <div id="content">
                    <div className="desktop_bg fade" />
                    <div className="desktop">
                        {this.handleShowFolder()}
                    </div>
                </div>
                <div>
                    {this.props.redux_folder.folder.map( (element,idx) => {
                        if( element.open ){
                            return <SubWindow key={ 'window-'+idx } subIndex={idx} chosenFolder={ element }/>
                        }
                        return null;
                    })}
                </div>
            </Fragment>
        )
    }
}

function mapStateToProps(state){
    return state;
}

export default connect(mapStateToProps)(Folder);