import React, { PureComponent } from 'react';
import {connect} from 'react-redux';
import DraggableCore from 'react-draggable';
import styled from 'styled-components';
import className from 'classnames';
import * as actions from '../../actions';
import './subWindow.scss';
import {aboutMe,career,study} from '../submenulist/aboutMe';

class subWindow extends PureComponent{
    
    constructor(props){
        super(props);
        this.state = {
            contentCode:'',
            modalSizeClass: 'pop_maxsize',
            modalSizeBoolean:false,
        }
    }

    /*창 버튼 */
    selected = (index) => {
        if(this.closeYN !== 'Y')
            this.props.folderSelected(index);
        this.closeYN = '';
    }
    close = (index) => {
        this.closeYN = 'Y';
        this.props.folderClose(index);
    }

    /*스타일 */
    Subtitle = styled.li`
        padding:0.5rem 0 0.2rem;
        span{
            font-weight: bold;
        }
    `;

    aboutMeContentDOM = {
        AM : (
            <div>
                <div className="business_card">
                    <div className="card_name">
                        <span>{aboutMe.subMenu[0].name}</span>
                    </div>
                    <div className="card_birth_day">
                        <span>{aboutMe.subMenu[0].birthDay}</span>
                    </div>
                    <div className="card_school">
                        <span>{aboutMe.subMenu[0].school}</span>
                    </div>
                    <div className="card_text">
                        {aboutMe.subMenu[0].text}
                    </div>
                    <div className="card_logo_img">
                        <img src="./image/logo/HTML5_CSS_JavaScript.png" alt=""/>
                        <img src="./image/logo/java.png" alt=""/>
                        <img src="./image/logo/spring.png" alt=""/><br/>
                        <img src="./image/logo/Oracle.png" alt=""/>
                        <img src="./image/logo/nodejs.png" alt=""/><br/>
                        <img src="./image/logo/react.png" alt=""/>
                    </div>
                </div>
            </div>
            )
        ,CA : career.subMenu.map((key) => { 
                return  <div>
                            <div className="history_box">
                                <div className="history_date">
                                    <span>{key.date}</span>
                                </div>
                                <div className="history_info">
                                    <div className="history_company">
                                        <span>회사</span><br/>{key.company}
                                    </div>
                                    <div className="history_part">
                                        <span>직종</span><br/>{key.part}
                                    </div>
                                    <div className="history_text">
                                        <span>업무</span><br/>{key.text}
                                    </div>
                                    <div className="history_skill">
                                        <span>기술</span><br/>{key.skill}
                                    </div>
                                </div>
                            </div>
                        </div>
                })
        ,ST :  study.subMenu.map((key) => { 
                return <div>
                            <div className="study_box">
                                <div className="study_date">
                                    <span>{key.date}</span>
                                </div>
                                <div className="study_info">
                                    <div className="study_company">
                                        <span>교육</span><br/>{key.company}
                                    </div>
                                    <div className="study_title">
                                        <span>과정명</span><br/>{key.title}
                                    </div>
                                    <div className="study_skill">
                                        <span>기술</span><br/>{key.skill}
                                    </div>
                                </div>
                            </div>
                        </div>
                })        
    }

    aboutMeContent = (code) => {
        switch (code) {
            case 'AM':
                return this.aboutMeContentDOM.AM;
            case 'CA':
                return this.aboutMeContentDOM.CA;
            
            case 'ST':
                return this.aboutMeContentDOM.ST;    
                
            default: 
                return this.aboutMeContentDOM.AM;
        }
    }

    domContent = (name) => {
        switch (name) {
            case 'user-home':
                console.log("aboutMe : ",aboutMe.subMenu);
                return ( 
                        <div className="pop_content">
                            <div className="pop_preview">
                                <div className="menu-list">
                                    <ul>
                                        <li className="user-menu-1" onClick={() => this.setState({ ...this.state, contentCode: aboutMe.code}) } >
                                            {aboutMe.name}
                                        </li>
                                        <li className="user-menu-2" onClick={() => this.setState({ ...this.state, contentCode: career.code}) }>
                                            {career.name}   
                                        </li>
                                        <li className="user-menu-3" onClick={() => this.setState({ ...this.state, contentCode: study.code}) }>
                                            {study.name}
                                        </li>
                                    </ul>
                                </div>
                                <div className="sub-content">
                                    {this.aboutMeContent(this.state.contentCode)}
                                </div>
                            </div>
                        </div>
                        );
        
            default:
                return ( <div className="pop_content">
                            <div className="">
                            <ul>
                                <li></li>
                                <li></li>
                            </ul>
                            </div>
                            <div className="subready">
                                <span> 현재 준비 중입니다. </span>
                            </div>
                        </div>
                        );
        }
    } 

    modalSizeChange = () => {
        if(this.state.modalSizeClass === 'pop_maxsize_on')
            this.setState({...this.state, modalSizeClass:'pop_maxsize'   ,modalSizeBoolean:false,});
        else
            this.setState({...this.state, modalSizeClass:'pop_maxsize_on',modalSizeBoolean:true ,});
    }

    createModal = () => {
        return (
              <DraggableCore  
                handle=".pop_head" 
                disabled={this.state.modalSizeBoolean}
                >
                <div className={className("modal",{"max_modal":this.state.modalSizeBoolean})}  
                     onMouseDownCapture={ () => {this.selected(this.props.chosenFolder.index) } }
                     style={{ zIndex: this.props.chosenFolder.z_index }}
                    >
                    <div className="pop_head">    
                        <div className="pop_folder_img"> 
                            <img src={this.props.chosenFolder.imgUrl} alt="" />
                        </div>
                        <div className="pop_title">
                            <span className={className({"text_color_ddd" : !this.props.chosenFolder.chosen})}>
                                { this.props.chosenFolder.text }
                            </span>
                        </div>
                        <div className="pop_mng">
                            <ul>
                                <li>
                                    <input type="button" className="pop_minsize"/>
                                </li>
                                <li onClick={() => {this.modalSizeChange()} }>
                                    <input type="button" className={this.state.modalSizeClass}/>
                                </li>
                                <li onClick={ () => { this.close(this.props.chosenFolder.index) } }>
                                    <input type="button" className="pop_close" />
                                </li>
                            </ul>
                        </div>
                    </div>
                    {/*
                    <nav>
                        <div>
                            <input className="pop_navi" type="text" alt="" />
                        </div>
                        <div>
                            <input className="pop_search" type="text" alt="" />
                        </div>
                    </nav> 
                    */}
                    { this.domContent(this.props.chosenFolder.name) }                    
                </div>
            </DraggableCore>
            )
    }
    
    render(){
        return(
            <div>
                {this.createModal()}
            </div>
        )   
    }
}

const mapToDispatch = (dispatch) =>({
     folderSelected: (index) => dispatch(actions.folderSelected(index)),
     folderClose   : (index) => dispatch(actions.folderClose(index)),
});

export default connect(null,mapToDispatch)(subWindow);